import React, { useRef } from "react";
import "./paste.min.css"
import 'animate.css';

import {useState } from "react";
import { Link,useParams } from "react-router-dom";
import { message, Pagination } from 'antd';

import PubSub from 'pubsub-js'

import { unstable_batchedUpdates } from "react-dom";



import { 
   CloseOne
 } from "@icon-park/react";

export default function Paste(){
   

   const pastelists = [
      {
         id: 1,
         name:'pinkgirl',
         length: 12,
         url: [
            "pinkgirl1.png",
            "pinkgirl2.png",
            "pinkgirl3.png",
            "pinkgirl4.png",
            "pinkgirl5.png",
            "pinkgirl6.png",
            "pinkgirl7.png",
            "pinkgirl8.png",
            "pinkgirl9.png",
            "pinkgirl10.png",
            "pinkgirl11.png",
            "pinkgirl12.png",
         ],
      },
      {
         id: 2,
         name:'textblock',
         length: 6,
         url: [
            "textblock1.png",
            "textblock2.png",
            "textblock3.png",
            "textblock4.png",
            "textblock5.png",
            "textblock6.png",
         ],
      },
      {
         id: 3,
         name:'popgirl',
         length: 16,
         url: [
            "popgirl1.png",
            "popgirl2.png",
            "popgirl3.png",
            "popgirl4.png",
            "popgirl5.png",
            "popgirl6.png",
            "popgirl7.png",
            "popgirl8.png",
            "popgirl9.png",
            "popgirl10.png",
            "popgirl11.png",
            "popgirl12.png",
            "popgirl13.png",
            "popgirl14.png",
            "popgirl15.png",
            "popgirl16.png",
         ],
      },
      {
         id: 4,
         name:'y2k',
         length: 21,
         url: [
            "y2k1.png",
            "y2k2.png",
            "y2k3.png",
            "y2k4.png",
            "y2k5.png",
            "y2k6.png",
            "y2k7.png",
            "y2k8.png",
            "y2k9.png",
            "y2k10.png",
            "y2k11.png",
            "y2k12.png",
            "y2k13.png",
            "y2k14.png",
            "y2k15.png",
            "y2k16.png",
            "y2k17.png",
            "y2k18.png",
            "y2k19.png",
            "y2k20.png",
            "y2k21.png",
         ],
      },
   ]

   //切换贴纸
   const [ischoose,setIschoose] = useState(false);
   const [pasteid,setPasteid] = useState(1);
   const [pastecurindex,setPastecurindex] = useState(-1)
   //分页
   const [current, setCurrent] = useState(1);

   const choosechange = (id)=>{
      if(ischoose===false) setCurrent(1);
      unstable_batchedUpdates(()=>{
         setIschoose(!ischoose)
         setPasteid(id);
         setPastecurindex(-1);
      })
   }
   const onChange = (page) => {
      setCurrent(page);
   };

   const pastecount = useRef(0);

   //点击单个贴纸
   const onpasteclick = (index)=>{
      if(pastecount.current<10){
         pastecount.current = pastecount.current+1;
         setPastecurindex((current-1)*12+index+1)
      }
      else{
         message.info('每页最多只能加10张贴纸哦～')
      }
   }


   const pastedispaly = (
      <>
         {
         current*12>pastelists[pasteid-1].length?
            pastelists[pasteid-1].url.splice(12*(current-1)).map((item,index)=>{
               return(
                  <li key={item} className="contentx">
                     <img  onClick={()=>onpasteclick(index)} src={require(`../../../../../../../assets/paste/content/${pasteid}/${item}`)} alt="pastenav1" />
                  </li>
               )  
            })
         
         : (
            current===1?
             pastelists[pasteid-1].url.splice(12*(current-1),12*current).map((item,index)=>{
               return (
                  <li key={item} className="contentx">
                     <img  onClick={()=>onpasteclick(index)} src={require(`../../../../../../../assets/paste/content/${pasteid}/${item}`)} alt="pastenav1" />
                  </li>
               )
             })
            :
            pastelists[pasteid-1].url.splice(12*(current-1)+1,12*current).map((item,index)=>{
               return (
                  <li key={item} className="contentx">
                     <img onClick={()=>onpasteclick(index)} src={require(`../../../../../../../assets/paste/content/${pasteid}/${item}`)} alt="pastenav1" />
                  </li>
               )
            }
            )
         ) 
         }
      </>
      )


   //发布贴纸组件选中的贴纸信息，让scrapbookx组件订阅
   PubSub.publish('imgurl',{gid: pasteid, imgid:pastecurindex});

   //关闭
   let params = useParams(); 
   let sid = params.sid;

    return(
       <div className="pastewrapper animate__animated animate__slideInDown">
         <div className="pasteheader">
            <ul className="pastenav">
               <Link to={`/admin/myscrapbook/scrapbookx/${sid}`} > 
                  <CloseOne className="close" theme="outline" size="35" fill="#5d3d6b"/>
               </Link>
               <li className={pasteid===1&&ischoose ?"navx choose":"navx"} onClick={()=>choosechange(1)}><img  src={require("../../../../../../../assets/paste/nav/nav1.png")} alt="pastenav1" /></li>
               <li className={pasteid===2&&ischoose ?"navx choose":"navx"} onClick={()=>choosechange(2)}><img  src={require("../../../../../../../assets/paste/nav/nav2.png")} alt="pastenav2" /></li>
               <li className={pasteid===3&&ischoose ?"navx choose":"navx"} onClick={()=>choosechange(3)}><img  src={require("../../../../../../../assets/paste/nav/nav3.png")} alt="pastenav3" /></li>
               <li className={pasteid===4&&ischoose ?"navx choose":"navx"} onClick={()=>choosechange(4)}><img  src={require("../../../../../../../assets/paste/nav/nav4.png")} alt="pastenav4" /></li>
            </ul>
         </div>
         <div className="pastelist"  >  
            <div className="dividepage">
            <Pagination  current={current} pageSize={12} onChange={onChange} total={pastelists[pasteid-1].length}  />
            </div>
            <ul className="pastecontent">       
              {pastedispaly}
            </ul>
            
         </div>
       </div>
    )
 }