import React from "react";
import { Link } from "react-router-dom";
import './register.min.css';


import logo from '../../assets/island_logo.png';

//引入icon-park
import { Home} from "@icon-park/react";
import '@icon-park/react/styles/index.css'

import { Form,
         Input,
         Button,
         message
} from "antd";

import { reqRegister } from "../../api";


export default function Register(){
   const formItemLayout = {
      labelCol: {
        xs: {
          span: 24,
        },
        sm: {
          span: 8,
        },
      },
      wrapperCol: {
        xs: {
          span: 24,
        },
        sm: {
          span: 16,
        },
      },
    };

   const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };
   
    const [form] = Form.useForm();
    const submit = () => {
       form.validateFields()
       .then(async (values) => {
         //请求注册
          const {username,password,email} = values
          const result = await reqRegister(username,password,email)
 
          // console.log('成功了',response)
          if(result.status===0){//注册成功
             message.success('注册成功')
 
             //跳转到登陆页面
             window.location.replace('/login')
          }else{//注册失败
             //提示错误信息
             message.error(result.msg)
          }
        }).catch((err)=>{
          console.log('校验失败',err)
        });
       }

    return(
       <div className="pagebody2" >
         <header className="head2">
            <nav>
               <img alt="island首页" src={logo} className="icon-logo"/>
               <Link to="/"><Home preserveAspectRatio="none" className="icon-home" theme="two-tone" size="35" fill={['#ffffff' ,'#ffbed3']}/></Link>
               <button className="login" ><Link style={{ textDecoration:'none',color:'white'}}  to="/login">登录</Link></button>
            </nav>
         </header>
         <section className="regform2">
            <img alt="island首页" src={logo} style={{height: '51px',width:'205px',marginTop:'30px',marginLeft:'200px'}}/>
            <div className="antform">
               <Form
               {...formItemLayout}
               form={form}
               name="register"
               className="antform"
               scrollToFirstError>
               <Form.Item
               className="nickname"
               name="username"
               label="用户名"
               tooltip="想让其他人叫你什么?"
               rules={[
                  {
                     required: true,
                     message: '请输入用户名!',
                     whitespace: true,
                  },
               ]}
               >
               <Input />
               </Form.Item>
               <Form.Item
                  className="password"
                  name="password"
                  label="密码"
                  rules={[
                     {
                        required: true,
                        message: '请输入密码!',
                     },
                  ]}
                  hasFeedback
                  >
                <Input.Password />
               </Form.Item>
               <Form.Item
                  className="confpassword"
                  name="confirm"
                  label="确认密码"
                  dependencies={['password']}
                  hasFeedback
                  rules={[
                     {
                        required: true,
                        message: '请确认你的密码!',
                     },
                     ({ getFieldValue }) => ({
                        validator(_, value) {
                        if (!value || getFieldValue('password') === value) {
                           return Promise.resolve();
                        }
                        return Promise.reject(new Error('两次密码不一致!'));
                        },
                     }),
                  ]}
                  >
               <Input.Password />
               </Form.Item>          
               <Form.Item
                  className="email"
                  name="email"
                  label="邮箱"
                  rules={[
                     {
                        type: 'email',
                        message: '邮箱格式不正确!',
                     },
                     {
                        required: true,
                        message: '请输入E-mail!',
                     },
                  ]}>
                     <Input />
               </Form.Item>
               <Form.Item {...tailFormItemLayout}>
                  <Button className="regi" type="primary" htmlType="submit" onClick={submit}>
                     注册
                  </Button>
               </Form.Item>
               </Form>
            </div>  
         </section>
       </div>
    )
 }



