import React from "react";
import "./editpaste.min.css"
import 'animate.css';


import { Link,useParams } from "react-router-dom";
import { useState } from 'react';
import { Col, InputNumber, Row, Slider } from 'antd';

import PubSub from 'pubsub-js'


import { 
   CloseOne
 } from "@icon-park/react";
export default function Editpaste(){

   //关闭
   let params = useParams(); 
   let sid = params.sid;

   const [inputValue1, setInputValue1] = useState(0);
   const onChange1 = (value) => {
    if (isNaN(value)) {
      return;
    }
    setInputValue1(value);
  };

  const [inputValue2, setInputValue2] = useState(0);
  const onChange2 = (value) => {
   if (isNaN(value)) {
     return;
   }
   setInputValue2(value);
 };

   //发布修改贴纸样式信息，让scrapbookx组件订阅
   let editpasteindex = params.index;
   PubSub.publish('editpaste',{editindex: editpasteindex, transparent:inputValue1, saturate:inputValue2});

    return(
       <div className="editpastewrapper animate__animated animate__slideInDown">
         <div className="editpasteheader">
            <ul className="pastenav">
               <Link to={`/admin/myscrapbook/scrapbookx/${sid}`} > 
                  <CloseOne className="close" theme="outline" size="35" fill="#5d3d6b"/>
               </Link>
               <span style={{position:'absolute',marginTop:'13px',fontSize:'20px',color:'#5d3d6b',fontWeight:'300'}}>编辑</span>
            </ul>
         </div>
         <div className="editpastelist"  >  
            <div className="transparent">
               <span className="title">不透明度</span>
               <div className="adjust">
                  <Row>
                     <Col span={12}>
                     <Slider
                        min={0}
                        max={1}
                        onChange={onChange1}
                        value={typeof inputValue1 === 'number' ? inputValue1 : 0}
                        step={0.01}
                     />
                     </Col>
                     <Col span={4}>
                     <InputNumber
                        min={0}
                        max={1}
                        style={{
                           margin: '0 16px',
                        }}
                        step={0.01}
                        value={inputValue1}
                        onChange={onChange1}
                     />
                     </Col>
                  </Row>
               </div>
           
            </div>
            <div className="saturate">
               <span className="title">不饱和度</span>
               <div className="adjust">
                  <Row>
                     <Col span={12}>
                     <Slider
                        min={0}
                        max={1}
                        onChange={onChange2}
                        value={typeof inputValue2 === 'number' ? inputValue2 : 0}
                        step={0.01}
                     />
                     </Col>
                     <Col span={4}>
                     <InputNumber
                        min={0}
                        max={1}
                        style={{
                           margin: '0 16px',
                        }}
                        step={0.01}
                        value={inputValue2}
                        onChange={onChange2}
                     />
                     </Col>
                  </Row>
               </div>
            </div>
         </div>
       </div>
    )
 }