import React from "react";
import { useState,useRef,useEffect } from "react";
import './guest.min.css';
import 'animate.css';


import logo from '../../assets/island_logo.png';
import bear from './images/bear.png';

//引入icon-park
import { Home,
         Earth,
         CalendarThirtyTwo,
         Like,
} from "@icon-park/react";
import '@icon-park/react/styles/index.css'

export default function Guest(){
   const timmer = useRef()
   const [Year, setYear] = useState('');
   const [Month, setMonth] = useState('');
   const [Day, setDay] = useState('');

   const getNewDate = () => {
      const time = new Date();
      const year = time.getFullYear();
      const month = time.getMonth() + 1;
      const day = time.getDate();
   
      setYear(year)
      setMonth(month)
      setDay(day)
    }

    useEffect(() => {
      timmer.current = setInterval(getNewDate, 1000);
      return () => {
        clearTimeout(timmer.current)
      }
      // eslint-disable-next-line
    }, [])

    

    return(
       <div className="pagebody" >
         <header className="head">
            <nav>
               <img alt="island首页" src={logo} className="icon-logo"/>
               <Home preserveAspectRatio="none" className="icon-home" theme="two-tone" size="35" fill={['#ffffff' ,'#ffbed3']}/>
               <Earth className="language" theme="two-tone" size="16" fill={['#ffffff' ,'#ffbed3']}/>
               <span className="language-text">简体中文</span>
               <button className="login" onClick={()=>{window.location.replace('login');}}>登录</button>
               <button className="register" onClick={()=>{window.location.replace('/register')}}>注册</button>
            </nav>
         </header>
         <aside className="aside">
            <div className="calender">
               <span className="month">{Month}</span> 
               <span className="year">/{Day}/{Year}</span> 
            </div>
            <CalendarThirtyTwo className="calender-img" theme="outline" size="77" fill="#ffffff"/>
         </aside>
         <section className="introduction">
            <div className="title1 animate__animated animate__fadeInUp">ISLAND每个人都是独一无二的岛屿</div>
            <div className="title2 animate__animated animate__fadeInUp">沉浸式在线手帐制作网站</div>
            <section className="bottombox1">
               <Like className="icon-heart" theme="outline" size="31" fill="#ffffff"/>
               <div className="saying">希望似阳光，驱散迷雾，照亮前进的路</div>
            </section>
            <section className="bottombox2">
               <Like className="icon-heart" theme="outline" size="31" fill="#ffffff"/>
               <div className="saying">魅力是一种内在美，而不是妩媚的面貌和动人的体态</div>
            </section>
            <img className="bear" src={bear} alt="bear" />
         </section>
       </div>
    )
 }