import React from "react";
import { Link } from "react-router-dom";
import './login.min.css';

import logo from '../../assets/island_logo.png';

//引入icon-park
import { Home} from "@icon-park/react";
import '@icon-park/react/styles/index.css'

import { Form,
         Input,
         Button,
         message,
} from "antd";


import { reqLogin } from "../../api";
import {onLogin} from "../../api/cookie"


export default function Login(){
   const formItemLayout = {
      labelCol: {
        xs: {
          span: 24,
        },
        sm: {
          span: 8,
        },
      },
      wrapperCol: {
        xs: {
          span: 24,
        },
        sm: {
          span: 16,
        },
      },
    };

   const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };
   
   const [form] = Form.useForm();
   const submit = () => {
      form.validateFields()
      .then(async (values) => {
        //请求登陆
         const {username,password} = values
         const result = await reqLogin(username,password)

         // console.log('成功了',response)
         if(result.status===0){//登陆成功
            onLogin(username)
            message.success('登陆成功')
            //跳转到admin页面
            window.location.replace('/admin')
         }else{//登陆失败
            //提示错误信息
            message.error(result.msg)
         }
       }).catch((err)=>{
         console.log('校验失败',err)
       });
      }
      
    return(
       <div className="pagebody1" >
         <header className="head1">
            <nav>
               <img alt="island首页" src={logo} className="icon-logo"/>
               <Link to="/"><Home preserveAspectRatio="none" className="icon-home" theme="two-tone" size="35" fill={['#ffffff' ,'#ffbed3']}/></Link>
               <button className="register" ><Link style={{ textDecoration:'none',color:'white'}}  to="/register">注册</Link></button>
            </nav>
         </header>
         <section className="regform1">
            <img alt="island首页" src={logo} style={{height: '51px',width:'205px',marginTop:'30px',marginLeft:'200px'}}/>
            <div className="antform">
               <Form
               {...formItemLayout}
               form={form}
               name="register"
               className="antform"
               scrollToFirstError>
               <Form.Item
               className="username"
               name="username"
               label="用户名"
               tooltip="What do you want others to call you?"
               rules={[
                  {
                     required: true,
                     message: '用户名必须输入',
                  },
                  {
                     min: 4,
                     message: '用户名至少4位',
                  },
                  {
                     max: 12,
                     message: '用户名不能超过12位',
                  },
                  {
                     pattern: /^[a-zA-Z0-9_]+$/,
                     message: '用户名必须是英文、数字、下划线组成',
                  },
               ]}
               >
               <Input />
               </Form.Item>
               <Form.Item
                  className="password"
                  name="password"
                  label="密码"
                  rules={[
                     {
                     required: true,
                     message: '密码必须输入',
                     },
                     {
                        min: 4,
                        message: '密码至少4位',
                     },
                     {
                        max: 12,
                        message: '密码不能超过12位',
                     },
                     {
                        pattern: /^[a-zA-Z0-9_]+$/,
                        message: '必须是英文、数字、下划线组成',
                     },
                  ]}
                  hasFeedback
                  >
                <Input.Password />
               </Form.Item>
               <Form.Item {...tailFormItemLayout}>
                  <Button className="logi" type="primary" htmlType="submit" onClick={submit}>
                     登陆
                  </Button>
               </Form.Item>
               </Form>
            </div>  
         </section>
       </div>
    )
 }


