/*
包含应用中所有接口请求函数的模块
 */

import ajax from "./ajax";

const BASE = ''

//登陆
export const reqLogin= (username,password)=>{
    return ajax(BASE+'/login',{username,password},'POST')
}

export const reqRegister= (username,password,email)=>{
    return ajax(BASE+'/register',{username,password,email},'POST')
}

export const reqCreatetime= (username)=>{
    return ajax(BASE+'/admin',{username},'POST')
}

export const reqCreatescrapbook= (username,coverID,scrapbookname)=>{
    return ajax(BASE+'/admin/create/scrapbook',{username,coverID,scrapbookname},'POST')
}

export const reqScrapbooks = (username)=>{
    return ajax(BASE+'/admin/myscrapbook',{username},'POST')
}

export const reqDeleteScrapbook = (scrapid)=>{
    return ajax(BASE+'/admin/myscrapbook/delete',{scrapid},'POST')
}

export const reqEditscrapbook= (editid,coverID,scrapbookname)=>{
    return ajax(BASE+'/admin/myscrapbook/edit',{editid,coverID,scrapbookname},'POST')
}

export const reqScrappapers= (cid)=>{
    return ajax(BASE+'/admin/myscrapbook/scrapbookx',{cid},'POST')
}

export const reqAddpaper= (papercoverid,cid)=>{
    return ajax(BASE+'/admin/myscrapbook/scrapbookx/addpaper',{papercoverid,cid},'POST')
}

export const reqPastes= (page,cid)=>{
    return ajax(BASE+'/admin/myscrapbook/scrapbookx/pastes',{page,cid},'POST')
}

export const savePastes= (page,cid,pastes,styles)=>{
    return ajax(BASE+'/admin/myscrapbook/scrapbookx/savepastes',{page,cid,pastes,styles},'POST')
}


export const reqDeletepaper = (index ,sid)=>{
    return ajax(BASE+'/admin/myscrapbook/scrapbookx/deletepaper',{index ,sid},'POST')
}
