import "./addpaper.min.css"
import {useEffect, useState} from 'react';
import { Link,useParams } from "react-router-dom";
import ReactDOM from 'react-dom';


//引入icon-park
import '@icon-park/react/styles/index.css'
import { 
   ArrowLeft,
   ArrowRight
 } from "@icon-park/react";

 import { 
   message, 
} from 'antd';

import {reqAddpaper} from '../../../../../../../api/index'

function Dialog(props) {
   const [node, setNode] = useState();

   useEffect(() => {
       const element = document.createElement('div');
       document.body.appendChild(element);
       setNode(element);
       return () => {
           document.body.removeChild(element);
       };
   }, []);

   return node ? ReactDOM.createPortal(props.children, node) : null;
}


export default function Addpaper(){
     //当前页面切换
     const [curpage,setCurpage] = useState(1)
     const pagefront = ()=>{
        if(curpage>1){
         setCurpage(curpage-1);
      }
        else{
           message.info('已经是第一页')
        }

        
     }
     const pagerear = ()=>{
         if(curpage<8)  setCurpage(curpage+1);
         else{
            message.info('已经是最后一页')
         }
      }

   //选中和取消
   const [isRed,setRed]=useState(false); 
   const [cid,setCid] = useState(0);

   let params = useParams(); 
   let sid = params.sid;

   const handleChoose=(num)=>{
       //管理是否选中相应纸张
       setRed(isRed?false:true);
       setCid(num);
       
   }


   //新建纸张
   const createpaper = ()=>{
      let papercoverid = (curpage-1)*2+cid;
      reqAddpaper(papercoverid,sid).then((res,err)=>{
         if(res.status===0){//创建纸张成功
            message.success(res.msg)
            window.location.reload();
            // setPapers(res.data)
            }else{//获取失败
                  //提示错误信息
            message.error(res.msg)
            }
      })
   }
 


    return(
      <Dialog>
      <div className='addwrapper'>
          <div className='formtitle'>
              <span>请选择纸张样式</span>
          </div>
          <ul id="scrapbookpaper">
              <li className={isRed&&cid===1?'turnred cvr':'cvr'} onClick={()=>handleChoose(1)}><img src={require(`../../../../../../../assets/papercovers/paper${(curpage-1)*2+1}min.png`)} alt="cvr1" /></li>
              <li className={isRed&&cid===2?'turnred cvr':'cvr'} onClick={()=>handleChoose(2)}><img src={require(`../../../../../../../assets/papercovers/paper${(curpage-1)*2+2}min.png`)} alt="cvr1" /></li>
          </ul>
          <div className="front" onClick={pagefront}><ArrowLeft  theme="two-tone" size="24" fill={['#ffffff' ,'#ffbed3']}/></div>
          <div className="rear" onClick={pagerear}><ArrowRight theme="two-tone" size="24" fill={['#ffffff' ,'#ffbed3']}/></div>
          <span className="page">{curpage}</span>
          <button className='yes' onClick={createpaper}>确定</button>
          <Link to={`/admin/myscrapbook/scrapbookx/${sid}`} ><button className='no'  >取消</button></Link>
         
      </div>
  </Dialog>
    )
 }