// 应用的根组件
import React from "react";
import { BrowserRouter,Route,Routes} from "react-router-dom";



//各个页面引入
import Login from './pages/login/login'
import Admin from './pages/admin/admin'
import Administrator from './pages/administrator/administrator' 
import Guest from './pages/guest/guest'
import Register from './pages/register/register';
import Couture from "./pages/admin/childs/couture/couture";
import Wardrobe from "./pages/admin/childs/wardrobe/wardrobe";
import Create from "./pages/admin/childs/mybook/create/create"
import Mydairy from "./pages/admin/childs/mybook//mydairy/mydairy";
import Myscrapbook from "./pages/admin/childs/mybook//myscrapbook/myscrapbook";
import Dairyx from "./pages/admin/childs/mybook//mydairy/dairyx/dairyx";
import Scrapbookx from "./pages/admin/childs/mybook//myscrapbook/scrapbookx/scrapbookx";
import Usermanage from "./pages/administrator/childs/usermanage/usermanage";
import Bgmusicmanage from "./pages/administrator/childs/bgmusicmanage/bgmusicmange";
import Templates from "./pages/admin/childs/mybook/myscrapbook/components/templates/templates";
import Addpaper from "./pages/admin/childs/mybook/myscrapbook/components/addpaper/addpaper";
import Deletepaper from "./pages/admin/childs/mybook/myscrapbook/components/deletepapper/deletepaper";
import Paste from "./pages/admin/childs/mybook/myscrapbook/components/paste/paste";
import Text from "./pages/admin/childs/mybook/myscrapbook/components/text/text";
import Editpaste from "./pages/admin/childs/mybook/myscrapbook/components/editpaste/editpaste";


export default function App(){
   return(
      <BrowserRouter> 
      <Routes>
            <Route path="/login" element={<Login />}></Route>
            <Route path="/admin" element={<Admin />}></Route>
            <Route path="/admin/couture" element={<Couture />}></Route>
            <Route path="/admin/wardrobe" element={<Wardrobe />}></Route>
            <Route path="/admin/create" element={<Create />}></Route>
            <Route path="/admin/mydairy" element={<Mydairy />}></Route>
            <Route path="/admin/mydairy/dairyx" element={<Dairyx />}></Route>
            <Route path="/admin/myscrapbook" element={<Myscrapbook />}></Route>
            <Route path="/admin/myscrapbook/scrapbookx/:cid" element={<Scrapbookx />}>
                     <Route path="templates"  element={<Templates />}></Route>
                     <Route path="addpaper/:sid" element={<Addpaper />}></Route>
                     <Route path="deletepapper/:sid/:pagerange" element={<Deletepaper />}></Route>
                     <Route path="paste/:sid" element={<Paste />}></Route>
                     <Route path="editpaste/:sid/:index" element={<Editpaste />}></Route>
                     <Route path="text" element={<Text />}></Route>
                   
            </Route>



            <Route path="/administrator" element={<Administrator />}></Route>
            <Route path="/administrator/usermanage" element={<Usermanage />}></Route>
            <Route path="/administrator/bgmusicmanage" element={<Bgmusicmanage />}></Route>


            <Route path="/" element={<Guest />}></Route>

            <Route path="/register" element={<Register />}></Route>
      </Routes>
      </BrowserRouter>
   )
}