import "./deletepaper.min.css"
import {useEffect, useState,useRef} from 'react';
import { Link,useParams } from "react-router-dom";
import ReactDOM from 'react-dom';


//引入icon-park
import '@icon-park/react/styles/index.css'
 import { 
   Select, 
   Space,
   message
} from 'antd';



import {reqDeletepaper} from '../../../../../../../api/index'

function Dialog(props) {
   const [node, setNode] = useState();

   useEffect(() => {
       const element = document.createElement('div');
       document.body.appendChild(element);
       setNode(element);
       return () => {
           document.body.removeChild(element);
       };
   }, []);

   return node ? ReactDOM.createPortal(props.children, node) : null;
}


export default function Deletepaper(){
    
   let params = useParams(); 
   let sid = params.sid;
   const totalrange = params.pagerange;

   const choosepage = useRef(1)

   //删除纸张
   const deletepaper = ()=>{
      reqDeletepaper(choosepage.current-1 ,sid).then((res,err)=>{
         if(res.status===0){//创建纸张成功
            message.success(res.msg)
            window.location.reload();
            }else{//获取失败
                  //提示错误信息
            message.error(res.msg)
            }
      })
   }
 
   const handleChange = (value) => {
      choosepage.current = value;
    };

   let pagerange=[];
   useEffect(()=>{
      for(let i =0; i<totalrange;i++){
         pagerange.push({
            value:`${i+1}`,
            label:`${i+1}`,
         })
      }
   },[])
  
    return(
      <Dialog>
      <div className='deletewrapper'>
          <div className='formtitle'>
              <span>请输入要删除的纸张页码</span>
          </div>
          <div className="pagerange">
               <Space wrap>
                  <Select
                     defaultValue="1"
                     style={{
                     width: 220,
                     }}
                     onChange={handleChange}
                     options={pagerange}
                  />
               </Space>
          </div>
          <button className='yes' onClick={deletepaper}>确定</button>
          <Link to={`/admin/myscrapbook/scrapbookx/${sid}`} ><button className='no'  >取消</button></Link>
         
      </div>
  </Dialog>
    )
 }