import './bgmusic.min.css'
import { Dropdown, 
} from 'antd';

import { useState } from 'react';

import { 
    MusicOne
} from "@icon-park/react";
import '@icon-park/react/styles/index.css'


export default function Bgmusic(){
  
    const [songname,setSongname] = useState('')
    function handleplay(){
        let audio_player = document.getElementById('audio_player');
        if (audio_player.paused) {
          audio_player.play();
        } else {
          audio_player.pause();
        }
      }

      const onClick = ({ key }) => {
        setSongname(items[key-1].label)
        let audio_player = document.getElementById('audio_player');


        if(key==='1'){
            audio_player.src= require('../../assets/bgmusic/srr.mp3')
        }else if(key==='2'){
            audio_player.src = require('../../assets/bgmusic/dbw.mp3')
        }else if(key==='3'){
            audio_player.src = require('../../assets/bgmusic/hpf.mp3')
        }else if(key==='4'){
            audio_player.src = require('../../assets/bgmusic/tinnis.mp3')
        }else if(key==='5'){
            audio_player.src = require('../../assets/bgmusic/fnl.mp3')
        }    
        audio_player.play();
      };

      const items = [
        {
          key: '1',
          label: (
            <span >
              Sugar rush ride
            </span>
          ),
        },
        {
          key: '2',
          label: (
            <span>
              Devil by the window
            </span>
          ),
        },
        {
          key: '3',
          label: (
            <span>
              Happy Fools
            </span>
          ),
        },
        {
          key: '4',
          label: (
            <span>
              tinttus
            </span>
          ),
        },
        {
            key:'5',
            label: (
              <span>
                farewell neverland
              </span>
            ),
          },
      ];
 

    return(
      <div className='container'>
        <MusicOne className="music" theme="two-tone" size="91" fill={['#ffffff' ,'#ffbed3']}/>
        <Dropdown
            menu={{
            items,
            onClick,
            }}
        >
            <a onClick={(e) => e.preventDefault()}>
                <div className="songalbum" onClick={handleplay}>
                    <div className='mask'></div>
                </div>
            </a>
        </Dropdown>
        
        <div className='songnames'>
            <span className='songtext'>
            tommorrow x together<br/> {songname}
            </span>
        </div>
       
        <div id='audioBox'>
           <audio id="audio_player" loop >
            <source src={require('../../assets/bgmusic/srr.mp3')}/>
          </audio>
        </div>
       
      </div>   
    )
  
}


