import React from "react";
import { useState,useRef,useEffect } from "react";
import { Link } from "react-router-dom";
import logo from '../../assets/island_logo.png';
import './admin.min.css';
import character from '../../assets/defaultbear.png'
import 'animate.css';

//引入icon-park
import '@icon-park/react/styles/index.css'
import { reqCreatetime } from "../../api";

import Bgmusic from "../../components/bgmusic/bgmusic";
import { loginUser,logout } from '../../api/cookie' // 引入

import { 
   message,
} from 'antd';


export default function Admin(){
   const createtime = useRef()
   //获取创建时间
   const username = loginUser()
   reqCreatetime(username).then((res,err)=>{
      if(res.status===0){//获取创建时间成功
         createtime.current = res.msg
         }else{//获取失败
               //提示错误信息
         }
   })
   const timmer = useRef()
   const [Day, setDay] = useState('');
   const getNewDate = () => {
      const timenow =Date.parse(new Date());
      const time = timenow - createtime.current;
      const day = Math.floor(time/(1000 * 60 * 60 * 24));
      setDay(day)
    }

    useEffect(() => {
      timmer.current = setInterval(getNewDate, 1000);
      return () => {
        clearTimeout(timmer.current)
      }
      // eslint-disable-next-line
    }, [])


   const infonotdevelop = ()=>{
      message.info('该功能正在开发中，尽情期待哦～')
   }

    
   return(
   <div className="pagebody4" >
      <Bgmusic ></Bgmusic>
         <header className="head4">
            <nav>
               <img alt="island首页" src={logo} className="icon-logo"/>
               <Link to="/admin" className="nav1">首页</Link>
               <span  onClick={infonotdevelop}  className="checkin">签到</span>
               <span onClick={infonotdevelop}  className="nav2">服装店</span>
               <span onClick={infonotdevelop}  className="nav3">我的衣橱</span>
               <Link to="/admin/create" className="nav4">制作手帐本/日记</Link>
               <div className="pagemark"></div>
               <span className="logout" onClick={()=>{logout()}}>退出登陆</span>
            </nav>
         </header>
         <section className="maincontent">
               <div id="counttime" className="animate__animated animate__lightSpeedInLeft">
                  您已经使用ISLAND<span className="highlight">{Day+1}</span>天
               </div>
               <img alt="个人形象" src={character} className="character"/>
         </section>
         <img src="" alt="" />
       </div>
    )
 }