import {useEffect, useState} from 'react';
import ReactDOM from 'react-dom';

import { Input,
        message
 } from 'antd';

import "./popwindow.min.css"
import { loginUser} from '../../api/cookie' // 引入


import { reqCreatescrapbook,reqEditscrapbook } from "../../api";

import cvr1 from "../../assets/covers/cvr1.png";
import cvr2 from "../../assets/covers/cvr2.png";
import cvr3 from "../../assets/covers/cvr3.png";
import cvr4 from "../../assets/covers/cvr4.png";
import cvr5 from "../../assets/covers/cvr5.png";
import cvr6 from "../../assets/covers/cvr6.png";
import cvr7 from "../../assets/covers/cvr7.png";
 
function Dialog(props) {
    const [node, setNode] = useState();
 
    useEffect(() => {
        const element = document.createElement('div');
        document.body.appendChild(element);
        setNode(element);
        return () => {
            document.body.removeChild(element);
        };
    }, []);
 
    return node ? ReactDOM.createPortal(props.children, node) : null;
}


export default function Popwindow(props){
    const [isRed,setRed]=useState(false); 
    const [cid,setCid] = useState(0);
    const [scrapname,setScrapname] = useState('');
    const onChange = (e) => {
        setScrapname(e.target.value)
      };
    const handleChoose=(id)=>{
        //管理是否选中相应封面
        setRed(isRed?false:true);
        setCid(id);
    }

    //校验输入不能为空
    const validSubmit=(scrapbookname,coverID)=>{
         if(scrapbookname===''){
            message.error('手帐本名称不能为空');
            return false;
        }
         if(coverID===0){
            message.error('您未选择封面,请选择一个封面');
            return false;
        }
        return true;
    }

    const handleSubmit=() => {
            //提交数据
           const username = loginUser();
           const coverID = cid;
           const scrapbookname = scrapname;
           
           if(validSubmit(scrapbookname,coverID)){
            reqCreatescrapbook(username,coverID,scrapbookname).then((res,err)=>{
                if(res.status===0){
                    message.success('已创建成功,点击取消关闭窗口')
                   }else{//创建失败
                    //提示错误信息
                    message.error(res.msg)
                   }
             })
           }
           
        }
    
    const handleEdit=() =>{
        const coverID = cid;
        const scrapbookname = scrapname;
        if(validSubmit(scrapbookname,coverID)){
        let editid = props.editid;
         reqEditscrapbook(editid,coverID,scrapbookname).then((res,err)=>{
             if(res.status===0){
                 message.success('已修改成功')
                 window.location.reload();
                }else{//创建失败
                 //提示错误信息
                 message.error(res.msg)
                }
          })
        }
        
    }

    return(
        <Dialog>
            <div className='popwrapper'>
                <div className='formtitle'>
                    <span>手帐本基本信息</span>
                </div>
                <label  className='lab1'>手帐本名称:</label>
                <Input id="scrapbookname" className='input1' showCount maxLength={7} onChange={onChange} />
                <br></br>
                <br></br>
                <label  className='lab2'>手帐本封面:</label>
                <ul id="scrapbookcover">
                    <li className={isRed&&cid===1?'turnred cvr':'cvr'} onClick={()=>handleChoose(1)}><img src={cvr1} alt="cvr1" /></li>
                    <li className={isRed&&cid===2?'turnred cvr':'cvr'} onClick={()=>handleChoose(2)}><img src={cvr2} alt="cvr1" /></li>
                    <li className={isRed&&cid===3?'turnred cvr':'cvr'} onClick={()=>handleChoose(3)}><img src={cvr3} alt="cvr1" /></li>
                    <li className={isRed&&cid===4?'turnred cvr':'cvr'} onClick={()=>handleChoose(4)}><img src={cvr4} alt="cvr1" /></li>
                    <li className={isRed&&cid===5?'turnred cvr':'cvr'} onClick={()=>handleChoose(5)}><img src={cvr5} alt="cvr1" /></li>
                    <li className={isRed&&cid===6?'turnred cvr':'cvr'} onClick={()=>handleChoose(6)}><img src={cvr6} alt="cvr1" /></li>
                    <li className={isRed&&cid===7?'turnred cvr':'cvr'} onClick={()=>handleChoose(7)}><img src={cvr7} alt="cvr1" /></li>
                    <li className='selfupload'>自定义上传，敬请期待</li>
                </ul>
                <button className='yes' onClick={props.isedit==="true"? handleEdit :handleSubmit}>确定</button>
                <button className='no' onClick={props.click}>取消</button>
            </div>
        </Dialog>
    )
}